import React, { Component } from 'react';

class InputBox extends Component {
    constructor(props){
        super(props);
        this.inputItem = React.createRef();
    }


    render() {
      return (
        <div className="text-center" style={{marginTop:"3px", marginBottom:"3px"}}>
            <select ref={this.inputItem} className="ffxiInput" onChange={this.props.equipItem}>
                {this.props.itemData.map((equip) => 
                <option key={equip} value={equip}>{equip}</option>
                )}
            </select>
        </div>
      );
    }
}
  
export default InputBox;